import {
	logOut
} from "../../api/keycabint.js"
export default {
	data() {
		return {
			showPassword: false,
			inputType: "password",
			nickName: uni.getStorageSync('nickName'),
			avatarUrl: uni.getStorageSync('avatarUrl'),
		}
	},
	methods: {
		showpsw() {
			this.showPassword = !this.showPassword
			this.inputType = "text";
		},
		hidepsw() {
			this.showPassword = !this.showPassword;
			this.inputType = "password";
		},
		userLogout() {
			logOut().then(res => {
					if (res.error == 0) {
						uni.clearStorageSync();
						uni.navigateTo({
							url: "../login/login"
						});
					}
				})
		}
	}
}
